.page {
  height: 100vh;
  width: 100vw;
  display: grid;
  background-color:rgb(10, 25, 47)!important ;
}
.chat h2{
font-size: 2.6em;
margin-bottom: 40px;
}
.chat span{
  font-size: 1.4em;
}
.chat .time{
  display: block;
  font-size: 0.9em;
}
.messages{
  max-height: 300px;
  overflow: auto;
}
.messages::-webkit-scrollbar{
  width: 3px;
}
.messages::-webkit-scrollbar-track{
  background: #ddd;
}
.messages::-webkit-scrollbar-thumb{
  background: #aaa;
}

























































